

import awards__item_1 from "../../assets/награды/default/1.svg";
import awards__item_2 from "../../assets/награды/default/2.svg";
import awards__item_3 from "../../assets/награды/default/3.svg";
import awards__item_4 from "../../assets/награды/default/4.svg";
import awards__item_5 from "../../assets/награды/default/5.svg";
import awards__item_6 from "../../assets/награды/default/6.svg";
import awards__item_7 from "../../assets/награды/default/7.svg";
import awards__item_8 from "../../assets/награды/default/8.svg";
import awards__item_9 from "../../assets/награды/default/9.svg";
import awards__item_10 from "../../assets/награды/default/10.svg";
import awards__item_11 from "../../assets/награды/default/11.svg";


// bibliotek
import React from "react";
import Marquee from "react-fast-marquee";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import {useRef, useEffect} from "react";
// style
import "./awards.scss";

function Awards(){
    gsap.registerPlugin(ScrollTrigger);
    const awardsText =useRef(null);
    useEffect(()=>{
        const el = awardsText.current;
        gsap.fromTo(el, {opacity: 0, scale: 0.8, x:400}, { scale:1, opacity: 1, x:0, duration:3, scrollTrigger: {
        trigger: el,
        scrub:5,
        start: "top 70%",
        end: "+=200",
        }})
    }, []);
    return(
       <div className="awards__wrapper" id="contact">
        <h6 ref={awardsText}>Awards</h6>
            <div className="awards__container">
                <Marquee 
                    direction="right"
                    speed={150}
                    align="center"
                >
                        <div className="awards__item">
                            <img src={awards__item_1} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_2} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_3} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_4} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_5} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_6} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_7} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_8} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_9} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_10} alt="" />
                        </div>
                        <div className="awards__item">
                            <img src={awards__item_11} alt="" />
                        </div>                        
                </Marquee>
            </div>
       </div>
    )
}
export default Awards;